import rangeButton from "@/components/rangeButton-edit.vue";
let _this
export default {
	components:{
		rangeButton
	},
	data() {
		return {
			// SAME(1, "相同"),
			//  SIMILARITY(2, "相似"),
			//  DIFFERENT(3, "不同");
			items:[{id:1,label:'相同'},{id:2,label:'相似'},{id:3,label:'不同'}],
			partCompare:'',
			bigButtons: [{id:1,label:'相同'},{id:3,label:'不同'}],
			list:'',
			form:{
					inquiryAnalysisSampleCode:'',	    //询价分析数据编码
				    quotationAnalysisSampleCode:'',	//报价分析数据编码
				    ingredient:'',						//成分
					specification:'',				//规格
					style:'',							//风格
					clothType:	'',				//布样类型
					density:'',							//密度
					gramWeight:	'',					//克重
					clothBreadth:''	,				//幅宽
				
			},
			compareResult:'',
			quotationNumber:''
			
		};
	},
	onLoad:function(options){
		_this =this
		this.quotationNumber = options.quotationNumber
		this.compareResult = JSON.parse(options.result)
		this.form = this.compareResult
		console.log(this.compareResult)
		console.log(this.quotationNumber)
		this.contrastDetails()
	},
	methods:{
		initSetButton:function(){
			
		},
		compareChange:function(label,e){
			switch(label){
				case 'ingredient':
				   _this.form.ingredient = e
				   break
				case 'specification':
				   _this.form.specification = e
				    break
				case 'style':
				   _this.form.style = e
				    break
				case 'clothType':
				   _this.form.clothType = e
				    break
				case 'density':
				   _this.form.density = e
				    break
				case 'gramWeight':
				   _this.form.gramWeight = e
				    break
				case 'clothBreadth':
				   _this.form.clothBreadth = e
				    break
			}
			console.log(e)
			
		},
		contrastDetails:function(){
			let url = this.Api.contrastDetails
			let data = {
				quotationNumber: _this.quotationNumber
			}
			
			this.myRequest(data,url,'get').then(res => {
			  console.log(res)
			  if (res.data.status == 0){
				 _this.list = res.data.data
			  }
			  console.log(_this.list)
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		},
		
		bindCancel:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		confirm:function(){
			let url = this.Api.contrastUpdate
			let data = _this.form
			console.log(data)
			uni.showLoading({
				title:"加载中",
				mask: true
			})
			this.myRequest(data,url,'post').then(res => {
			  console.log(res)
			  if (res.data.status == 0){
				  uni.hideLoading()
				  var pages = getCurrentPages();
				  var currPage = pages[pages.length - 1]; //当前页面
				  var prevPage = pages[pages.length - 2]; //上一个页面
				 
				  //直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				  
				  prevPage.setData({
				     isDoRefresh:true
				  })
				  
				  uni.navigateBack({
				  	delta: 1
				  });
			  }
			}).catch(err => {
			  wx.showToast({
			    title: err.data.errMsg,
			    icon: 'none'
			  });
			});
		}
	}
}
